.text_inactive {
  color: #8585ad;
}
.text_60 {
  margin-top: 60px;
  margin-bottom: 60px;
}
.order {
  display: flex;
  margin: 113px 60px;
  align-items: center;
  flex-direction: column;
}
.text_main {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 2.2;
  font-weight: normal;
  font-family: "Jet Brains Mono";
}
.text_main_medium {
  padding: 0;
  font-size: 24px;
  margin-top: 32px;
  line-height: 1.25;
  font-weight: bold;
  font-family: "Jet Brains Mono";
}
.number {
  margin: 0;
  padding: 0;
  font-size: 144px;
  line-height: 0.83;
  font-family: "Iceland";
  text-shadow:
    0 0 16px rgba(51, 51, 255, 0.25),
    0 0 8px rgba(51, 51, 255, 0.25),
    0 4px 32px rgba(51, 51, 255, 0.5);
}
